	.post
	{
	    background-color: #FFF;
	    overflow: hidden;
	    box-shadow: 0 0 1px #CCC;
	    min-height:70vh; 
	    margin-bottom: 20px;
	    @include border-shadow();
		
		.preview{
			min-height: 15vh;
		}
		.content
		{
		    padding: 15px;
		}

		.author
		{
		    font-size: 11px;
		    color: #737373;
		    padding: 25px 30px 20px;
		}

		.post-img-content
		{
		    height: 200px;
		    overflow: hidden;
		    position: relative;
		}

		.post-img-content img
		{
		    position: absolute;
		}

		.post-title
		{
		    display: table-cell;
		    vertical-align: bottom;
		    z-index: 2;
		    position: relative;
		}

		.post-title b
		{
		    background-color: rgba(51, 51, 51, 0.58);
		    display: inline-block;
		    margin-bottom: 5px;
		    color: #FFF;
		    padding: 10px 15px;
		    margin-top: 5px;
		}
	} 

	.social-buttons{
		a{
			margin: 0px 5px;
			i{
				font-size: 2em;
			}
		}
		.facebook{
			color:#3b5998;
		}
		.twitter{
			color:#1da1f2;
		}
		.pinterest{
			color:#bd081b;
		}
		.gplus{
			color:#d43e34;
		}
	}