body {
  background-color: #f5f5f5;
}

#panel body {
  overflow: hidden;
}

#panel #body {
  overflow-y: auto;
  max-height: 95.5vh;
}

#panel .footer {
  position: absolute;
  bottom: 0;
  width: 100%;
  height: 35px;
  background-color: #f5f5f5;
}

#front {
  /* Main body section */
  /* small screen */
}

#front .badge {
  background-color: #fff;
  color: #333;
}

#front :focus {
  outline: none;
}

#front .side-menu {
  position: fixed;
  width: 300px;
  height: 100%;
  background-color: #028fcc;
  border-right: 1px solid #e7e7e7;
  z-index: 100;
}

#front .side-menu .navbar {
  border: none;
}

#front .side-menu .navbar-header {
  width: 100%;
  border-bottom: 1px solid #e7e7e7;
}

#front .side-menu .navbar-nav .active a {
  background-color: transparent;
  margin-right: -1px;
  border-right: 5px solid #e7e7e7;
}

#front .side-menu .navbar-nav li {
  display: block;
  width: 100%;
  border-bottom: 1px solid #e7e7e7;
}

#front .side-menu .navbar-nav li a {
  color: #fff;
  padding: 15px;
}

#front .side-menu .navbar-nav li a .glyphicon {
  padding-right: 10px;
}

#front .side-menu #dropdown {
  border: 0;
  margin-bottom: 0;
  border-radius: 0;
  background-color: transparent;
  box-shadow: none;
}

#front .side-menu #dropdown .caret {
  float: right;
  margin: 9px 5px 0;
}

#front .side-menu #dropdown .indicator {
  float: right;
}

#front .side-menu #dropdown > a {
  color: #fff;
  border-bottom: 1px solid #e7e7e7;
}

#front .side-menu #dropdown .panel-body {
  padding: 0;
  background-color: #f3f3f3;
}

#front .side-menu #dropdown .panel-body .navbar-nav {
  background-color: #028fcc;
  width: 100%;
}

#front .side-menu #dropdown .panel-body .navbar-nav li {
  padding-left: 15px;
  border-bottom: 1px solid #e7e7e7;
}

#front .side-menu #dropdown .panel-body .navbar-nav li:last-child {
  border-bottom: none;
}

#front .side-menu #dropdown .panel-body .panel > a {
  margin-left: -20px;
  padding-left: 35px;
}

#front .side-menu #dropdown .panel-body .panel-body {
  margin-left: -15px;
}

#front .side-menu #dropdown .panel-body .panel-body li {
  padding-left: 30px;
}

#front .side-menu #dropdown .panel-body .panel-body li:last-child {
  border-bottom: 1px solid #e7e7e7;
}

#front .side-menu #search-trigger {
  background-color: #f3f3f3;
  border: 0;
  border-radius: 0;
  position: absolute;
  top: 0;
  right: 0;
  padding: 15px 18px;
}

#front .side-menu .brand-name-wrapper {
  min-height: 50px;
}

#front .side-menu .brand-name-wrapper .navbar-brand {
  display: block;
}

#front .side-menu #search {
  position: relative;
  z-index: 1000;
}

#front .side-menu #search .panel-body {
  padding: 0;
}

#front .side-menu #search .panel-body .navbar-form {
  padding: 0;
  padding-right: 0px;
  width: 100%;
  margin: 0;
  position: relative;
  border-top: 1px solid #e7e7e7;
}

#front .side-menu #search .panel-body .navbar-form .form-group {
  width: 100%;
  position: relative;
}

#front .side-menu #search .panel-body .navbar-form .input-group {
  width: 100%;
}

#front .side-menu #search .panel-body .navbar-form .input-group input {
  border: 0;
  border-radius: 0;
  box-shadow: none;
  width: 93%;
  height: 50px;
}

#front .side-menu #search .panel-body .navbar-form .btn {
  position: absolute;
  right: 0;
  top: 0;
  border: 0;
  border-radius: 0;
  background-color: #f3f3f3;
  padding: 15px 18px;
}

#front .side-body {
  margin-left: 310px;
}

