#front #side-navbar {
  background-color: #028fcc;
}

#front #side-navbar .navbar-header {
  background-color: #028fcc;
}

#front #side-navbar .navbar-header .brand-wrapper {
  background-color: #028fcc;
}

#front #side-navbar .navbar-header .brand-wrapper .navbar-toggle {
  background-color: #028fcc;
}

#front #side-navbar .navbar-header .brand-wrapper .navbar-toggle .icon-bar {
  background-color: #fff;
}

#front #side-navbar .navbar-header .brand-wrapper a {
  background-color: inherit;
  color: #fff;
}

#front #side-navbar .side-menu-container .slide-in {
  background-color: #028fcc;
}

#front #side-navbar .side-menu-container .slide-in li a, #front #side-navbar .side-menu-container .slide-in li a[aria-expanded='false'] {
  color: #fff;
}

#front #side-navbar .side-menu-container .slide-in li a:hover, #front #side-navbar .side-menu-container .slide-in li a:focus, #front #side-navbar .side-menu-container .slide-in li a[aria-expanded='false']:hover, #front #side-navbar .side-menu-container .slide-in li a[aria-expanded='false']:focus {
  background-color: rgba(0, 0, 0, 0.2);
}

#front nav .container {
  padding-left: 0px;
  padding-right: 0px;
}

#front nav #navbar {
  background-color: #028fcc;
}

#front nav #navbar ul {
  background-color: #028fcc;
}

#front nav #navbar ul .open .dropdown-toggle {
  background-color: rgba(0, 0, 0, 0.2);
}

#front nav #navbar ul li ul {
  background-color: #028fcc;
}

#front nav #navbar ul li a {
  color: #fff;
}

#front nav #navbar ul li a:hover {
  background-color: rgba(0, 0, 0, 0.2);
}

#front nav #navbar .form-control, #front nav #navbar button {
  border-radius: 0px;
}

#front #body {
  background-color: #fff;
  -webkit-box-shadow: 0px 0px 6px 1px rgba(0, 0, 0, 0.25);
  -moz-box-shadow: 0px 0px 6px 1px rgba(0, 0, 0, 0.25);
  box-shadow: 0px 0px 6px 1px rgba(0, 0, 0, 0.25);
}

#front #body #wrap-block {
  margin-bottom: 10px;
}

#front #body .breadcrumb {
  background-color: transparent;
}

#front #body .section .panel-default {
  -webkit-box-shadow: 0px 0px 6px 1px rgba(0, 0, 0, 0.25);
  -moz-box-shadow: 0px 0px 6px 1px rgba(0, 0, 0, 0.25);
  box-shadow: 0px 0px 6px 1px rgba(0, 0, 0, 0.25);
}

#front #body .section .interes {
  background-color: #5bc0de;
  color: #fff;
  padding: 10px;
}

#front .related .img-body {
  min-height: 25vh;
}

#front .img-body {
  min-height: 45vh;
}

#front .post {
  background-color: #FFF;
  overflow: hidden;
  box-shadow: 0 0 1px #CCC;
  min-height: 70vh;
  margin-bottom: 20px;
  -webkit-box-shadow: 0px 0px 6px 1px rgba(0, 0, 0, 0.25);
  -moz-box-shadow: 0px 0px 6px 1px rgba(0, 0, 0, 0.25);
  box-shadow: 0px 0px 6px 1px rgba(0, 0, 0, 0.25);
}

#front .post .preview {
  min-height: 15vh;
}

#front .post .content {
  padding: 15px;
}

#front .post .author {
  font-size: 11px;
  color: #737373;
  padding: 25px 30px 20px;
}

#front .post .post-img-content {
  height: 200px;
  overflow: hidden;
  position: relative;
}

#front .post .post-img-content img {
  position: absolute;
}

#front .post .post-title {
  display: table-cell;
  vertical-align: bottom;
  z-index: 2;
  position: relative;
}

#front .post .post-title b {
  background-color: rgba(51, 51, 51, 0.58);
  display: inline-block;
  margin-bottom: 5px;
  color: #FFF;
  padding: 10px 15px;
  margin-top: 5px;
}

#front .social-buttons a {
  margin: 0px 5px;
}

#front .social-buttons a i {
  font-size: 2em;
}

#front .social-buttons .facebook {
  color: #3b5998;
}

#front .social-buttons .twitter {
  color: #1da1f2;
}

#front .social-buttons .pinterest {
  color: #bd081b;
}

#front .social-buttons .gplus {
  color: #d43e34;
}

